export default {
    name: "state",
    data(){
        return{
            stateValue: '',
            scroll: null
        }
    },
    mounted(){
        this.getInfo();
    },
    methods:{
        states(){
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.editWorkStatus,
                type: "post",
                data: {
                    work_status: this.stateValue
                },
                success: (response, allResponse) => {
                    console.log('response', allResponse)
                }
            });
        },
        uptasa(){
            if(this.stateValue != 2){
                this.stateValue = '2';
                this.states();
            }
        },
        uptasb(){
            if(this.stateValue != 1){
                this.stateValue = '1';
                this.states();
            }
        },
        uptasc(){
            if(this.stateValue != 0){
                this.stateValue = '0';
                this.states();
            }
        },
        getInfo(){
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.getMine,
                type: "post",
                data: {
                },
                success: (response, allResponse) => {
                    this.stateValue = response.work_status
                    this.stateValue = this.stateValue.toString();
                }
            });
        },
        onScroll (pos) {
            this.scroll = pos.left
        },
    },
    watch:{
        scroll(){
            if(this.scroll < -50){
                this.$router.push('/operation')
            }
        }
    }
}



